import * as React from 'react';
import PropTypes from 'prop-types';
import Button from '@mui/material/Button';
import { styled } from '@mui/material/styles';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import DialogActions from '@mui/material/DialogActions';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import Typography from '@mui/material/Typography';
import { useDispatch } from 'react-redux';
import { toggleModal } from '../utility';
import CreatePostComponent from './post/createPost';
import { useLocation, useNavigate, useSearchParams } from 'react-router-dom';
import CreateCommentComponent from './comment/createComment';
import CreateTodoComponent from './todo/createTodo';

const BootstrapDialog = styled(Dialog)(({ theme }) => ({
    '& .MuiDialogContent-root': {
        padding: theme.spacing(2),
    },
    '& .MuiDialogActions-root': {
        padding: theme.spacing(1),
    },
}));

const BootstrapDialogTitle = (props) => {
    const { children, onClose, ...other } = props;

    return (
        <DialogTitle sx={{ m: 0, p: 2 }} {...other}>
            {children}
            {onClose ? (
                <IconButton
                    aria-label="close"
                    onClick={onClose}
                    sx={{
                        position: 'absolute',
                        right: 8,
                        top: 8,
                        color: (theme) => theme.palette.grey[500],
                    }}
                >
                    <CloseIcon />
                </IconButton>
            ) : null}
        </DialogTitle>
    );
};

BootstrapDialogTitle.propTypes = {
    children: PropTypes.node,
    onClose: PropTypes.func.isRequired,
};

const ModalDialogs = () => {
    const navigate = useNavigate()
    const { search, state } = useLocation()
    const handleClose = () => {
        navigate(-1)
    };
    let actionType
    let componentName
    const componentToBeRender = (() => {
        actionType = state ? "Update" : "Create New"

        if (search.includes('post')) {
            componentName = "Post"
            return <CreatePostComponent />;
        }
        else if (search.includes('comment')) {
            componentName = "Comment"

            return <CreateCommentComponent />
        }
        else if (search.includes('todo')) {
            componentName = "Todo"
            return <CreateTodoComponent />
        }
    })()
    return (
        <div>
            <BootstrapDialog
                // onClose={handleClose}
                aria-labelledby="customized-dialog-title"
                open={true}
            >
                <BootstrapDialogTitle id="customized-dialog-title" onClose={handleClose}>
                    {`${actionType} ${componentName}`}
                </BootstrapDialogTitle>
                <DialogContent dividers>
                    {componentToBeRender}
                </DialogContent>

            </BootstrapDialog>
        </div>
    );
}

export default ModalDialogs